import { useState } from "react"


let Item=()=>{
    let [onHover,setHover]=useState(false)
    let changeBehavior=()=>{
        setHover(!onHover)
    }
    return<div className="col-md-4 mt-2">
        <div className="card">
            <img src="https://renome-club.ru/upload/resize_cache/iblock/21b/450_450_1/21b820db0bcb2e59d5c7b038f9c5ff04.jpg" 
            className={onHover ? "ItemOn" : "ItemExit"}
            onMouseEnter={changeBehavior} onMouseLeave={changeBehavior} />
            <div className="card-body">
                brand 
                <div className="right">
                    price
                </div>
            </div>
        </div>
    </div>
}

export default Item