import { Formik, Field } from "formik";

let FilterRims = () => {
  return (
    <Formik initialValues={{ color: {} }} onSubmit={() => {}}>
      {({ values,handleSubmit }) => {
        return (
          <form onSubmit={handleSubmit}>
            <div className="row">
              <div className="col-md-4 d-flex center">
                <div className="m-2"><label htmlFor="color"><h5>Цвет</h5></label></div>
                <div>
                <select id="color" name="color" className="form-control">
                  <option value="NY">Красный</option>
                  <option value="SF">Синий</option>
                  <option value="CH">Желтый</option>
                </select></div>
              </div>
              <div className="col-md-4 d-flex center">
                <div className="m-2"><label htmlFor="material"><h5>Материал</h5></label></div>
                <div>
                <select id="material" name="material" className="form-control">
                  <option value="NY">Металл</option>
                  <option value="SF">Пластик</option>
                </select></div>
              </div>
              <div className="col-md-4 d-flex center">
                <div className="m-2"><label htmlFor="shape"><h5>Форма</h5></label></div>
                <div>
                <select id="shape" name="shape" className="form-control">
                  <option value="NY">Квадратные</option>
                  <option value="SF">Круглые</option>
                </select></div>
              </div>
            </div>
            <div className="row">
              <div className="col-md-4 d-flex center">
                <div className="m-2"><label htmlFor="brand"><h5>Брэнд</h5></label></div>
                <div>
                <select id="brand" name="brand" className="form-control">
                  <option value="NY">Brioni</option>
                  <option value="SF">Gucci</option>
                </select></div>
              </div>
              <div className="col-md-4 d-flex center">
                <div className="m-2"><label htmlFor="isAvailable"><h5>В наличии</h5></label></div>
                <div>
                <input id="isAvailable" name="isAvailable"
                 type="checkbox" className="mt-3"/>
                </div>
              </div>
              <div className="col-md-4 d-flex center">
                <div>
                <select name="sort" className="form-control">
                  <option value="NY">Сначала Новые</option>
                  <option value="SF">Сначала Подешевле</option>
                  <option value="SN">Сначала Подороже</option>
                </select></div>
              </div>
            </div>
            <button className="btn btn-outline-primary w-100">
              Применить
            </button>
          </form>
        );
      }}
    </Formik>
  );
};

export default FilterRims;
